/* ------------------------------------------------

 Helpers:
 - color
 - style
 - align

------------------------------------------------ */
/* ------ ALIGN */
.text-center,
.field-textarea {
  text-align: center; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-justify {
  text-align: justify; }

.text-ellipsis {
  white-space: nowrap;
  text-overflow: ellipsis; }

/* ------ COLORS */
.is-grey-text {
  color: #A9A9A9; }

.is-dark-grey-text {
  color: #808080; }

.is-white-text,
a.is-white-text,
a.is-white-text:hover {
  color: white; }

.is-black-text {
  color: #0a0a0a; }

.is-primary-text,
a.is-primary-text,
a.is-primary-text:hover {
  color: #ff470f; }

/* ------ WEIGHT */
.is-light {
  font-weight: 300; }

.is-normal,
.is-regular {
  font-weight: 400; }

.is-medium, .field-input,
.field-textarea {
  font-weight: 500; }

.is-semi-bold {
  font-weight: 600; }

.is-bold {
  font-weight: 700; }

.is-extra-bold {
  font-weight: 800; }

/* ------ STYLE */
.is-italic {
  font-style: italic; }

.is-uppercase {
  text-transform: uppercase; }

.is-underline,
a.is-underline,
a:link.is-underline {
  text-decoration: underline; }

.is-lowercase {
  text-transform: lowercase; }

.is-capitalized {
  text-transform: capitalize; }

.is-index-bottom {
  z-index: -1; }

.is-index-middle {
  z-index: 0; }

.is-index-top {
  z-index: 1; }

.wrap-1 > *,
.wrap-2 > *,
.wrap-3 > *,
.wrap-4 > *,
.wrap-5 > * {
  flex: 0 0 auto; }

.wrap-1 > * {
  width: 100%; }

.wrap-2 > * {
  width: 50%; }

.wrap-3 > * {
  width: 33.333333%; }

.wrap-4 > * {
  width: 25%; }

.wrap-5 > * {
  width: 12.5%; }

.infinite-scroller {
  height: auto;
  transition: height 1s ease; }

.app.modal-open {
  max-height: 100vh;
  overflow-y: hidden; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.modal {
  z-index: 10000;
  justify-content: center;
  align-items: center;
  background-color: rgba(10, 10, 10, 0.8);
  display: none;
  opacity: 0;
  will-change: opacity;
  overflow: auto; }
  .modal.active {
    display: flex;
    opacity: 1;
    animation: fadeIn 0.5s ease; }
  .modal .modal-dialog {
    text-align: center;
    transition-timing-function: ease-out;
    transition-property: transform;
    position: relative;
    padding: 4rem;
    will-change: transform, opacity;
    backface-visibility: hidden;
    background-color: white; }
    .modal .modal-dialog button.close {
      background-color: transparent;
      top: 1rem;
      right: 1rem;
      border: none;
      position: absolute;
      cursor: pointer;
      z-index: 10; }
    .modal .modal-dialog .modal-content {
      overflow: initial;
      width: auto;
      max-width: 1024px; }

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(359deg); } }

.spinner {
  text-align: center; }
  .spinner img {
    vertical-align: middle;
    margin-right: 1rem;
    -webkit-animation: rotation 1s infinite linear; }
  .spinner .content {
    position: relative;
    display: inline-block; }
    .spinner .content:after {
      content: attr(data-dots);
      position: absolute;
      right: -1.5rem;
      width: 1rem;
      height: 100%;
      text-align: left; }

